<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">我的任务</div>
    <el-tabs class="personnal-detail-wrap-tabs" v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="未完成" name="first"></el-tab-pane>
      <el-tab-pane label="已完成" name="second"></el-tab-pane>
    </el-tabs>
    <div class="personnal-detail-wrap-container">
      <common-card
        :option="cardOption"
        v-for="(c,i) in cards"
        :key="i"
        :datas="c"
        :status="estate"
        @clickHandler="clickHandler"
        @clickComment="clickComment"
      />
    </div>
    <bottom-pager :total="total" :pageSize="pagesize" :currentPage="currentPage" @change="pageChange"/>

    <el-dialog class="cp_detail" title="随堂练习" :visible.sync="dialogPSVisible" :before-close="resetForm">
      <div class="cp_title">题数：{{plTotal}}，总分：{{plScore}}</div>
      <div class="mb">
        <div class="mb">{{psData.PSSerial}}. [{{fetchPSType(psData.PSType)}}] {{psData.PSScore}}分</div>
        <div class="mb">{{psData.PSContent}}</div>
        <div class="mb" v-if="psData.PSType===1 || psData.PSType===2">
          <div v-for="(soItem, index) in soData" :key="index" :item="soItem">{{soItem.SOSerial}}. {{soItem.SOContent}}
          </div>
        </div>

        <do-answer
          :question-type="psData.PSType"
          :question-option="psData.PSOption"
          :answer-value="practiceAnswer"
          :module-picture="7"
          @change="answerChange">
        </do-answer>
        <div class="footer">
          <el-button type="success" v-show="currentIndex> 1" @click="submitAnswer(-1)">上一题</el-button>
          <el-button type="success" v-if="currentIndex< plTotal" @click="submitAnswer(1)">下一题</el-button>
          <el-button type="success" v-else-if="currentIndex == plTotal" @click="submitAnswer(1)">提交练习</el-button>
        </div>
      </div>
    </el-dialog>

    <el-dialog class="cp_detail" title="答题明细" :visible.sync="dialogDetailVisible">
      <div class="cp_title">题数：{{plTotal}}，总分：{{plScore}}</div>
      <div class="mb" v-for="(item, index) in cpDetail" :key="index">
        <div class="mb">{{item.PSData.PSSerial}}. [{{fetchPSType(item.PSData.PSType)}}] {{item.PSData.PSScore}}分</div>
        <div class="mb">{{item.PSData.PSContent}}</div>
        <div class="mb" v-if="item.PSData.PSType===1 || item.PSData.PSType===2">
          <div v-for="(soItem, index) in item.SOList" :key="index" :item="soItem">{{soItem.SOSerial}}.
            {{soItem.SOContent}}
          </div>
        </div>
        <div class="mb">
          答案：
          <span class="primary">{{item.PSData.PSAnswer}}</span>
        </div>
        <div class="mb">
          解析：
          <span class="danger">{{item.PSData.PSanalysis}}</span>
        </div>

        <div class="part_title">我的答题</div>
        <div v-html="item.PAContent"></div>
      </div>
    </el-dialog>

    <el-dialog title="课件评价" :visible.sync="dialogVisible" style="text-align: left;">
      <el-form ref="formComment" :model="formComment" :rules="rulesComment" size="small" label-width="80px">
        <el-form-item label="课件评分" prop="score">
          <el-input-number v-model="formComment.score" :min="1" :max="100" label="课件评分" controls-position="right"
                           :precision="0" :step="1"></el-input-number>
          分
        </el-form-item>
        <el-form-item label="课件评价" prop="comment">
          <el-input type="textarea" v-model="formComment.comment" :autosize="{ minRows: 3, maxRows: 10}"
                    placeholder="请输入课件评价"
                    maxlength="500" :show-word-limit="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="comment">
          <el-checkbox v-model="formComment.anonymous">匿名评价</el-checkbox>
        </el-form-item>
        <div style="text-align:right;">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button :loading="btnLoading" type="primary" @click="saveForm('formComment')">
            {{formComment.id ? '更新评价' : '保存' }}
          </el-button>
        </div>
      </el-form>
    </el-dialog>

  </div>
</template>
<script>
  import DoAnswer from "./doAnswer.vue";
  import commonCard from "../../../../components/personal/student/commonCard.vue";
  import bottomPager from "../../../../components/personal/bottomPager.vue";
  import api from "../../../../utils/api";

  export default {
    components: {
      DoAnswer,
      commonCard,
      bottomPager
    },
    data() {
      return {
        total: 0,
        currentPage: 1,
        pagesize: 20,
        estate: 1,
        activeName: "first",
        cardOption: {
          icon: "icon-homework",
          btnText: "去完成"
        },
        dialogPSVisible: false,
        dialogDetailVisible: false,
        cards: [],
        psData: [],
        paData: [],
        soData: [],
        cpDetail: [],
        practiceAnswer: "",
        ableReply: false,
        currentIndex: 1,
        cid: "",
        cpID: "",
        plID: "",
        plTotal: 0,
        plScore: 0,

        dialogVisible: false,
        formComment: {
          id: "",
          courseID: "",
          coursewareID: "",
          comment: "",
          anonymous: false,
          score: 100
        },
        rulesComment: {
          comment: {
            required: true,
            min: 1,
            max: 500,
            message: "长度在 1 到 500 个字符",
            trigger: "blur"
          },
          score: [
            { required: true, message: "课程评分不能为空" },
            { type: "number", message: "课程评分必须为数字值" }
          ]
        },
        btnLoading: false
      };
    },
    mounted() {
      this.GetStudentTaskDatas();
    },
    methods: {
      tabClick(val) {
        this.currentPage = 1;
        this.activeName = val.name;
        if (this.activeName === "second") {
          this.estate = 2;
        } else {
          this.estate = 1;
        }
        this.GetStudentTaskDatas();
      },
      GetStudentTaskDatas() {
        this.cards = [];
        this.$http
          .get(this.$api.GetStudentTaskDatas, {
            pno: this.currentPage,
            psize: this.pagesize,
            estate: this.estate,
            stuid: this.$http.getStu().StudentID
          })
          .then(res => {
            if (res.Result) {
              if ((res.Datas != null, res.Datas.length > 0)) {
                this.total = res.rowsCount;
                res.Datas.forEach(ele => {
                  var descs = [];
                  var attribute = ele.Attribute == 1 ? "课前" : "课后";
                  var type = ele.type == 1 ? "课件" : "随堂练习";
                  var btnText = "";
                  if (this.activeName === "second") {
                    if (ele.type == 1) btnText = "重新下载";
                    else btnText = "预览";
                  } else {
                    if (ele.type == 1) btnText = "下载";
                    else btnText = "去完成";
                  }
                  descs.push({
                    warn: false,
                    flag: ele.Attribute == 1,
                    time: ele.CreateTime,
                    teachername: ele.TeacherName,
                    coursename: ele.CourseName,
                    type: type,
                    attribute: attribute,
                    btnText: btnText
                  });
                  this.cards.push({
                    id: ele.id,
                    sid: ele.sid,
                    cid: ele.CourseID,
                    sname: ele.stitle,
                    title: ele.stitle,
                    descs: descs
                  });
                });
              }
            }
          })
          .catch(err => {
          });
      },
      pageChange(currentPage) {
        this.currentPage = currentPage;
        this.GetStudentTaskDatas();
      },
      clickHandler(ele) {
        if (ele.descs[0].btnText === "预览") {
          this.cid = ele.cid;
          this.cpID = ele.id;
          this.plID = ele.sid;

          this.GetPLStatsByPLID();
          this.getPracticeDetail();
        } else {
          if (ele.descs[0].type == "课件") {
            var urlstr = "";
            this.$http
              .get(this.$api.getConfigInfo, "")
              .then(res => {
                if (res.Result) {
                  urlstr = res.Datas.SkyDevicePath;
                  this.downloadFile(ele.id, ele.sid, urlstr);
                }
              })
              .catch(err => {
              });
          } else if (ele.descs[0].type == "随堂练习") {
            this.cid = ele.cid;
            this.cpID = ele.id;
            this.plID = ele.sid;
            this.dialogPSVisible = true;

            this.GetPLStatsByPLID();
            this.GetPSAndPA();
          }
        }
      },
      AddStuCoursewareTask(id, sid) {
        this.$http
          .post(this.$api.AddStuCoursewareTask, {
            cid: id,
            stuid: this.$http.getStu().StudentID
          })
          .then(res => {
            if (res.Result) {
              this.$message({
                dangerouslyUseHTMLString: true,
                showClose: true,
                message: "数据下载成功！",
                type: "success"
              });

              this.GetStudentTaskDatas();
            }
          })
          .catch(err => {
          });
      },

      downloadFile(id, sid, urlstr) {
        this.$http
          .get(this.$api.getMaterialDataByID, {
            ids: sid,
            Token: this.$http.getToken()
          })
          .then(res => {
            if (res.Result) {
              var downurl = this.$api.DownloadFileByMIDURL;
              window.location.href =
                downurl +
                "?mid=" + sid;
              this.AddStuCoursewareTask(id, sid);
            }
          })
          .catch(err => {
          });
      },

      GetPLStatsByPLID() {
        this.$http
          .get(this.$api.ClassPractisePLStatsByPLID, { plID: this.plID })
          .then(res => {
            if (res.Result) {
              this.plTotal = res.Total;
              this.plScore = res.Score;
            }
          })
          .catch(err => {
          });
      },

      //获取题目和答案
      async GetPSAndPA() {
        this.psData = [];
        this.paData = [];
        this.soData = [];
        this.practiceAnswer = "";
        let downloadURL = await api.GetI_DownloadFileURL();

        this.$http
          .get(this.$api.ClassPractiseGetPSAndPA, {
            cpID: this.cpID,
            plid: this.plID,
            psserial: this.currentIndex,
            StudentID: this.$http.getStu().StudentID
          })
          .then(res => {
            if (res.Result) {
              this.psData = res.PSData;
              this.paData = res.PAData;
              this.soData = res.SOData;
              if (this.paData != null) {
                if (this.psData.PSType == 4) {
                  var htmstr = this.paData.PAContent;
                  var newContent = htmstr.replace(
                    /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
                    function (match, capture) {
                      var filename = capture.split("/")[1];
                      var newStr =
                        '<img src="' +
                        downloadURL +
                        "?filename=" +
                        filename +
                        '&fileType=7" alt="" />';
                      return newStr;
                    }
                  );
                  this.practiceAnswer = newContent;
                } else {
                  this.practiceAnswer = this.paData.PAContent;
                }
              }
            }
          })
          .catch(err => {
          });
      },

      //获取题目类型
      fetchPSType(val) {
        let typeName = "";
        switch (val) {
          case 1:
            typeName = "单选题";
            break;
          case 2:
            typeName = "多选题";
            break;
          case 3:
            typeName = "判断题";
            break;
          case 4:
            typeName = "问答题";
            break;
        }

        return typeName;
      },

      answerChange(answer) {
        this.practiceAnswer = answer;
      },

      //提交答案
      submitAnswer(val) {
        var htmstr = $(".summernote").summernote("code");
        if (htmstr.length > 0) {
          this.practiceAnswer = htmstr.replace(
            /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
            function (match, capture) {
              var newStr =
                '<img src="picture/' +
                (capture.indexOf("=") > -1 ? capture.split("=")[1].split("&")[0] : "") +
                '/7" alt="" style="width:380px;"/>';
              return newStr;
            }
          );
        }
        if (!this.practiceAnswer) {
          this.$message({ showClose: true, message: "请作答", type: "error" });
        } else {
          let reqData = {
            cpid: this.cpID,
            paid: this.paData == null ? "" : this.paData.PAID,
            courseID: this.cid,
            psID: this.psData.PSID,
            studentID: this.$http.getStu().StudentID,
            paContent: this.practiceAnswer
          };

          this.$http
            .post(this.$api.ClassPractiseSubmitAnswer, reqData)
            .then(res => {
              if (res.Result) {
                if (this.currentIndex == this.plTotal && val > 0) {
                  this.submitPractice();
                } else {
                  this.currentIndex += val;
                  this.GetPSAndPA();
                }
              } else if (res.Info) {
                this.$message({ showClose: true, message: res.Info, type: "error" });
              }
            })
            .catch(err => {
            });
        }
      },

      resetForm() {
        this.dialogPSVisible = false;
        this.currentIndex = 1;
        this.practiceAnswer = "";
      },

      //获取学生答题
      async getPracticeDetail() {
        this.cpDetail = [];
        this.dialogDetailVisible = true;
        let downloadURL = await api.GetI_DownloadFileURL();
        this.$http
          .get(this.$api.GetCPStudentAnswer, {
            cpID: this.cpID,
            studentID: this.$http.getStu().StudentID
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                var newContent = "未作答";
                if (item.PAData != null) {
                  var htmstr = item.PAData.PAContent;
                  newContent = htmstr.replace(
                    /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
                    function (match, capture) {
                      var filename = capture.split("/")[1];
                      var newStr =
                        '<img src="' +
                        downloadURL +
                        "?filename=" +
                        filename +
                        '&fileType=7" alt="" style="width:380px;" />';
                      return newStr;
                    }
                  );
                }

                this.cpDetail.push({
                  PSData: item.PSData,
                  PAContent: newContent,
                  SOList: item.SOList
                });
              });
            }
          })
          .catch(err => {
          });
      },

      submitPractice() {
        this.$http
          .post(this.$api.ClassPractiseSubmitPractise, { cpID: this.cpID, studentID: this.$http.getStu().StudentID })
          .then(res => {
            if (res.Result) {
              this.resetForm();
              this.GetStudentTaskDatas();
              this.$message({ message: "提交成功", type: "success" });
            }
          })
          .catch(err => {
          });
      },

      // 课件点评
      clickComment(item) {
        this.formComment = {
          id: "",
          courseID: item["cid"],
          coursewareID: item["id"],
          studentID: this.$http.getStu().StudentID,
          comment: "",
          anonymous: false,
          score: 100
        };
        this.getCourseWareEvaluate();
        this.dialogVisible = true;
      },
      getCourseWareEvaluate() {
        let reqData = {
          courseID: this.formComment.courseID,
          studentID: this.$http.getStu().StudentID,
          cID: this.formComment.coursewareID
        };
        this.$http
          .get(this.$api.CoursewareCommentData, reqData)
          .then(res => {
            if (res.Result && res.Data) {
              this.formComment.id = res.Data["CCID"];
              this.formComment.comment = res.Data["Comment"];
              this.formComment.score = res.Data["Score"];
              this.formComment.anonymous = res.Data["IsAnonymous"] ? true : false;
            }
          });
      },
      saveForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              ccID: this.formComment.id,
              courseID: this.formComment.courseID,
              cID: this.formComment.coursewareID,
              studentID: this.$http.getStu().StudentID,
              score: this.formComment.score,
              comment: this.formComment.comment,
              isAnonymous: this.formComment.anonymous ? 1 : 0,
              type: 2
            };
            this.$http
              .post(this.$api.CoursewareCommentSave, reqData)
              .then(res => {
                if (res.Result) {
                  this.dialogVisible = false;
                  this.$message({
                    message: "提交成功！",
                    type: "success"
                  });
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },
    }
  };
</script>
<style lang="less" scoped>
  .personnal-detail-wrap {
    text-align: left;

    .cp_detail {
      .cp_title {
        margin-bottom: 20px;
        font-size: 15px;
      }

      .mb {
        margin-bottom: 15px;

        .primary {
          color: #409eff;
        }

        .danger {
          color: #f56c6c;
        }
      }

      .part_title {
        border-left: 2px solid #2a9efc;
        padding-left: 5px;
        margin: 5px 0;
      }

      .el-dialog__body {
        padding: 0px 20px 10px 20px;
      }

      .footer {
        margin-top: 20px;
        text-align: center;
      }
    }
  }
</style>



