<template>
  <div>
    <!-- 单选题 -->
    <div class="selects" v-if="questionType===1">
      <div class="content">
        <div class="prompt">请选择正确的答案</div>
        <div class="all-select">
          <div
            class="select-item"
            v-for="(item, i) in selectOptions"
            :key="i"
            :class="answerValue === item? 'singel-selected':''"
            @click="selectAnswer(item)"
          >{{item}}
          </div>
        </div>
      </div>
    </div>

    <!-- 多选题 -->
    <div class="selects" v-else-if="questionType===2">
      <div class="content">
        <div class="prompt">请选择正确的答案</div>
        <div class="all-select">
          <div
            class="select-item"
            v-for="(item, i) in selectOptions"
            :key="i"
            :class="answerValue.indexOf(item) > -1 ? 'singel-selected' : ''"
            @click="selectAnswer(item)"
          >{{item}}
          </div>
        </div>
      </div>
    </div>

    <!-- 判断题 -->
    <div class="answers" v-else-if="questionType===3">
      <div class="prompt">请选择正确的答案</div>
      <div class="buttons">
        <div
          v-for="(item, i) in judgeChoice"
          :key="i"
          :class="answerValue === item ? 'selected' : ''"
          @click="selectAnswer(item)"
        >{{item}}
        </div>
      </div>
    </div>

    <!-- 问答题 -->
    <div class="answers" v-else>
      <div class="prompt">请将您的答案写在下方的编辑框中</div>
      <summer-note ref="summerNote" :filetype="7"></summer-note>
    </div>
    <slot></slot>
  </div>
</template>
<script>
  import { newsetDesc } from "@/utils/utils";
  import summerNote from "@/components/personal/summernote.vue";

  export default {
    components: {
      summerNote
    },
    props: {
      questionType: {
        type: Number
      },
      questionOption: {
        type: Number,
        default: 0
      },
      btnName: {
        type: String,
        default: "提交"
      },
      modulePicture: {
        type: Number,
        default: 1
      },
      answerValue: {
        type: String,
        default: ""
      }
    },
    data() {
      return {
        judgeChoice: ["对", "错"],
        multiValue: this.selectValue ? this.selectValue.split("") : [],
        selectValue: this.answerValue
      };
    },
    mounted() {
    },
    methods: {
      selectAnswer(item) {
        if (this.questionType === 2) {
          let index = this.multiValue.indexOf(item);
          if (index > -1) {
            this.multiValue.splice(index, 1);
          } else {
            this.multiValue.push(item);
          }
          this.selectValue = this.multiValue.sort().join("");
        } else {
          this.selectValue = item;
        }
        this.backValue();
      },
      editorChange(value) {
        this.selectValue = value;
        this.backValue();
      },
      backValue() {
        this.$emit("change", this.selectValue);
      }
    },
    computed: {
      selectOptions() {
        if (this.questionType === 1) {
          return newsetDesc(this.questionOption);
        } else if (this.questionType === 2) {
          return newsetDesc(this.questionOption);
        } else if (this.questionType === 3) {
          return this.judgeChoice;
        }

        return "";
      }
    },
    watch: {
      questionType(newVal, oldVal) {
        if (newVal !== 4) {
          $(".summernote").summernote("code", "");
          $(".summernote").summernote("destroy");
        }
      },
      answerValue(newVal, oldVal) {
        if (newVal !== this.selectValue) {
          this.selectValue = newVal;
          this.multiValue = this.selectValue ? this.selectValue.split("") : [];
        }
        // if (this.questionType === 4) {
          $('#summernote').summernote('reset');
          setTimeout(() => {
            $(".summernote").summernote("code", newVal.length > 0 ? newVal : "");
          }, 500);
        // }
      }
    }
  };
</script>

<style lang='less' scoped>
  .selects {
    overflow: hidden;
    padding: 2px 0;
    background: rgba(255, 255, 255, 1);
    margin-bottom: 2px;

    .content {
      width: 600px;
    }

    .prompt {
      text-align: left;
      font-family: MicrosoftYaHei;
      color: rgba(51, 51, 51, 1);
    }

    .all-select {
      display: flex;
      justify-content: space-between;
      margin-top: 16px;

      .select-item {
        width: 40px;
        height: 40px;
        background: rgba(221, 228, 227, 1);
        display: inline-block;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
      }

      .singel-selected {
        color: #fff;
        background: rgb(24, 149, 233);
      }
    }
  }

  .answers {
    width: 100%;
    padding: 4px 0;
    background: rgba(255, 255, 255, 1);

    .quill-editor {
      background: #ebf0ef;
      margin-bottom: -4px;
    }

    .tt {
      font-size: 1.2px;
      color: #333333;
      margin-left: 4px;
      margin-bottom: 4px;

      .answer_delete {
        top: 0.2px;
      }
    }

    .buttons {
      display: flex;
      justify-content: space-around;

      div {
        width: 100px;
        height: 40px;
        background: rgba(221, 228, 227, 1);
        border-radius: 3px;
        font-size: 15px;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: #333;
        text-align: center;
        line-height: 40px;
      }

      .selected {
        color: #fff;
        background: rgb(24, 149, 233);
      }
    }
  }
</style>



